.userTitle {
	display: flex;
	justify-content: center;
	align-items: center;
}

.avatar {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 10px;
	width: 30px;
	height: 30px;
	background-color: #3e4e6c;
	color: #fff;
	border-radius: 50%;
	font-size: 17px;
	line-height: 0.965;
	font-weight: 900;
	text-align: center;
}

.userName {
	color: #7b8aa4;
	font-size: 17px;
	line-height: 0.965;
	font-weight: 400;
}

@media screen and (max-width: 767px) {
	.userName {
		display: none;
	}
	.avatar {
		margin-right: 0px;
	}
}
